import styles from "../css/About.module.scss";
import { FaEnvelope } from "react-icons/fa";
import { FaHtml5 } from "react-icons/fa";
import { FaCode } from "react-icons/fa";
import { FaToolbox } from "react-icons/fa";
import { FaDatabase } from "react-icons/fa";
const About = () => {
  return (
    <div className={styles.about} id="about">
      <h1>About Me</h1>
      <div className={styles.container}>
        <div className={styles.description}>
          <p>
            I am a software engineer based in San Diego, California, with a B.S.
            in Mathematics and Computer Science from the University of
            California, San Diego.
          </p>
          <p>
            Here is some more placeholder text until I can figure out what to
            say because holy crap I am realizing that I have no skills and no
            professional background.
          </p>
          <p>
            I am now typing another random paragraph just to get a feel of what
            my portfolio would look like if it actually had some kind of
            substance.
          </p>
        </div>
        <div className={styles.skills}>
          <div className={styles.languages}>
            <div className={styles.icon}>
              <FaCode className={styles.img} />
              Languages
            </div>
            <div className={styles.tags}>
              <div className={styles.tag}>Java</div>
              <div className={styles.tag}>C</div>
              <div className={styles.tag}>C++</div>
            </div>
          </div>
          <div className={styles.languages}>
            <div className={styles.icon}>
              <FaHtml5 className={styles.img} />
              Front End
            </div>
            <div className={styles.tags}>
              <div className={styles.tag}>HTML</div>
              <div className={styles.tag}>CSS</div>
              <div className={styles.tag}>Sass</div>
              <div className={styles.tag}>JavaScript</div>
              <div className={styles.tag}>React.js</div>
            </div>
          </div>
          <div className={styles.languages}>
            <div className={styles.icon}>
              <FaDatabase className={styles.img} />
              Back End
            </div>
            <div className={styles.tags}>
              <div className={styles.tag}>MySQL</div>
              <div className={styles.tag}>Node.js</div>
            </div>
          </div>
          <div className={styles.languages}>
            <div className={styles.icon}>
              <FaToolbox className={styles.img} />
              Tools
            </div>
            <div className={styles.tags}>
              <div className={styles.tag}>Linux CLI</div>
              <div className={styles.tag}>Git</div>
            </div>
          </div>
        </div>
      </div>
      <div className={styles.contact}>
        <h2>Contact Me</h2>
        <div className={styles.email}>
          <FaEnvelope /> nathany094@gmail.com
        </div>
      </div>
    </div>
  );
};

export default About;
